<script setup>
  import NavBar from '../components/Navbar.vue';
  import SideBar from '../components/Sidebar.vue';
</script>

<template>
  <div>
    <NavBar/>
    <SideBar/>
    <div class="main-content">
        <section class="section">
          <div class="section-header">
            <h1>Stakeholder Diagram</h1>
          </div>
          <div class="section-body">
            <div class="row">
              <div class="col-lg-3 col-md-6 col-sm-6 col-12">
                <div class="card card-statistic-1">
                  <div class="card-icon bg-primary">
                    <i class="fas fa-user"></i>
                  </div>
                  <div class="card-wrap">
                    <div class="card-header">
                      <h4>Stakeholder Kuadran A</h4>
                    </div>
                    <div class="card-body">
                      <!--                    {{ jumlahUser }}-->
                      20
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-lg-3 col-md-6 col-sm-6 col-12">
                <div class="card card-statistic-1">
                  <div class="card-icon bg-danger">
                    <i class="fas fa-building"></i>
                  </div>
                  <div class="card-wrap">
                    <div class="card-header">
                      <h4>Stakeholder Kuadran B</h4>
                    </div>
                    <div class="card-body">
                      <!--                    {{ jumlahRole }}-->
                      15
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-lg-3 col-md-6 col-sm-6 col-12">
                <div class="card card-statistic-1">
                  <div class="card-icon bg-warning">
                    <i class="fas fa-handshake"></i>
                  </div>
                  <div class="card-wrap">
                    <div class="card-header">
                      <h4>Stakeholder Kuadran C</h4>
                    </div>
                    <div class="card-body">
                      80
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-lg-3 col-md-6 col-sm-6 col-12">
                <div class="card card-statistic-1">
                  <div class="card-icon bg-success">
                    <i class="fas fa-map-marked"></i>
                  </div>
                  <div class="card-wrap">
                    <div class="card-header">
                      <h4>Stakeholder Kuadarn D</h4>
                    </div>
                    <div class="card-body">
                      47
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col-12 col-md-12 col-lg-6">
                    <div class="card card-primary">
                      <div class="card-header">
                        <h4>Percentage Stakeholder</h4>
                      </div>
                      <div class="card-body">
                        <apexchart
                            width="500"
                            height="500"
                            type="donut"
                            :options="chartOptions"
                            :series="series"
                        ></apexchart>
                      </div>
                    </div>
              </div>
              <div class="col-12 col-md-12 col-lg-6">
                <div class="card card-primary">
                  <div class="card-header">
                    <h4>Stakeholder Comparison</h4>
                  </div>
                  <div class="card-body">
                    <apexchart width="500" height="350" type="bar" :options="chartOptions2" :series="series2"></apexchart>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
    </div>
  </div>
</template>

<script>

export default {
  name: 'RoleManagement',
  data() {
    return {
      scriptElements: [],
      series: [20, 15, 80, 47],
      chartOptions: {
        chart: {
          width: 380,
          type: 'pie',
        },
        labels: ['Kuadran A', 'Kuadran B', 'Kuadran C', 'Kuadran D'],
        responsive: [{
          breakpoint: 480,
          options: {
            chart: {
              width: 200
            },
            legend: {
              position: 'bottom'
            }
          }
        }]
      },
      chartOptions2: {
        chart: {
          id: 'vuechart-example'
        },
        xaxis: {
          categories: ['Kuadran A', 'Kuadran B', 'Kuadran C', 'Kuadran D']
        }
      },
      series2: [{
        name: 'series-1',
        data: [20, 15, 80, 47]
      }]
    }
  },
  mounted() {
    this.dynamicallyLoadScript("../assets/js/stisla.js");
    this.dynamicallyLoadScript("../assets/js/scripts.js");
    this.dynamicallyLoadScript("../assets/js/custom.js");
  },
  beforeUnmount() {
    // Hapus elemen-elemen script sebelum komponen dihancurkan
    this.scriptElements.forEach((scriptElement) => {
      scriptElement.remove();
    });
  },
  methods: {
    dynamicallyLoadScript(url) {
      let script = document.createElement("script");
      script.src = url;
      this.scriptElements.push(script); // Simpan referensi elemen script
      document.body.appendChild(script);
    },
  },
}
</script>

